/* üåë ÂÖ®Â±èËÅäÂ§©Á™óÂè£ÔºàÊöóÈªëÊ®°ÂºèÔºâ */
.chat-window {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100vh; /* ËÆ©Á™óÂè£ÂÖÖÊª°Êï¥‰∏™ËßÜÂè£ */
    background-color: #1e1e1e; /* Ê∑±Ëâ≤ËÉåÊôØ */
    color: #ddd; /* ÈªòËÆ§Â≠ó‰ΩìÈ¢úËâ≤ */
}

/* Â§¥ÂÉèÊ†∑Âºè */
.avatar {
    position: absolute;
    top: 20px;
    right: 20px;
    width: 36px;
    height: 36px;
    background-color: #007bff;
    color: white;
    font-size: 16px;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    cursor: pointer;
}

/* üó® Ê∂àÊÅØÂÆπÂô® */
.messages-container {
    flex: 1;
    overflow-y: auto;
    padding: 10%;
    padding-top: 5%;
    display: flex;
    flex-direction: column;
    background-color: #1e1e1e;
    scrollbar-width: thin;
    scrollbar-color: #888 #2a2a2a;
}

/* üó® ÈÄöÁî®Ê∂àÊÅØÊ†∑Âºè */
.message {
    max-width: 70%;
    padding: 12px 16px;
    margin: 8px 0;
    border-radius: 12px;
    font-size: 16px;
    line-height: 1.5;
    word-break: break-word;
    white-space: pre-wrap; /* ‰øùÊåÅÊç¢Ë°åÊ†ºÂºè */
}

/* üßë‚Äçüíª Áî®Êà∑Ê∂àÊÅØÔºàÈù†Âè≥ÔºåËìùËâ≤Ê∞îÊ≥°Ôºâ */
.user-message {
    background-color: #444;
    color: #fff;
    align-self: flex-end;
}

/* ü§ñ AI Ê∂àÊÅØÔºàÈù†Â∑¶ÔºåÁÅ∞Ëâ≤Ê∞îÊ≥°Ôºâ */
.ai-message {
    color: #ddd;
    align-self: flex-start;
}

/* ‚å® ËæìÂÖ•Ê°ÜÂå∫Âüü */
.input-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 3%;
}

/* ‚å® ËæìÂÖ•Ê°Ü */
.input-container textarea {
    width: 100%;
    max-width: 600px;
    min-height: 60px;
    padding: 15px;
    border-radius: 20px;
    border: 1px solid #555;
    background-color: #2a2a2a;
    color: #ddd;
    font-size: 16px; /* Á°Æ‰øùÂ≠ó‰ΩìÂ§ßÂ∞è‰∏ÄËá¥ */
    font-family: "Arial", sans-serif; /* ËÆæÁΩÆÂ≠ó‰Ωì */
    transition: 0.3s;
    outline: none;
    resize: none;
    overflow-y: auto;
}

/* üöÄ ÂèëÈÄÅÊåâÈíÆ */
.input-container button {
    padding: 12px;
    background-color: #6f6f6f;
    color: #fff;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 18px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 10px; /* ‰ΩøÊåâÈíÆ‰∏éËæìÂÖ•Ê°ÜÊúâÈó¥Ë∑ù */
    transition: 0.3s;
}

/* üöÄ ÂèëÈÄÅÊåâÈíÆÂõæÊ†áÔºà‰ΩøÁî® Unicode Â≠óÁ¨¶Ôºâ */
.input-container button::before {
    content: "\1F680"; /* ‰ΩøÁî® Unicode Â≠óÁ¨¶Ë°®Á§∫ÂõæÊ†á (Envelope, ÂèëÈÄÅÂõæÊ†á) */
    font-size: 16px;
    color: #fff;
}

/* üîÑ Âä†ËΩΩÁä∂ÊÄÅ */
.loading-message {
    text-align: center;
    font-style: italic;
    color: #bbb;
    margin-top: 10px;
}

/* üéõ ÊªöÂä®Êù°‰ºòÂåñ */
.messages-container::-webkit-scrollbar {
    width: 6px;
}

.messages-container::-webkit-scrollbar-thumb {
    background-color: #666;
    border-radius: 3px;
}

.messages-container::-webkit-scrollbar-track {
    background-color: #2a2a2a;
}

.no-messages {
    color: #888;
    font-size: 18px;
    font-style: italic;
    text-align: center;
    margin-top: 20px;
}
